:root{
    --userHeight:null;
}

#container-login{
    position: relative;
    width: 100%;
    height:var(--userHeight);
    background-image: url(https://gw.alipayobjects.com/zos/rmsportal/TVYTbAXWheQpRcWDaDMu.svg);
    background-repeat: no-repeat;
    background-position: center 110px;
    background-size: 100%;
}
#loginLogo {
    position: absolute;
    left: 50%;
    top:50%;
    margin-top: -5%;
    width: 100px;
    height: 100px;
    background:url("../img/logo-1.png") no-repeat left center;
}
.antd-pro-login-login{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -200px;
    margin-left: -200px;
    width: 400px;
}
#scaffold-src-components-Login-demo-basic .icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    background: url('https://gw.alipayobjects.com/zos/rmsportal/itDzjUnkelhQNsycranf.svg');
    margin-left: 16px;
    vertical-align: middle;
    cursor: pointer;
}
#scaffold-src-components-Login-demo-basic .icon-alipay {
    background-position: -24px 0;
}
#scaffold-src-components-Login-demo-basic .icon-alipay:hover {
    background-position: 0 0;
}
#scaffold-src-components-Login-demo-basic .icon-taobao {
    background-position: -24px -24px;
}
#scaffold-src-components-Login-demo-basic .icon-taobao:hover {
    background-position: 0 -24px;
}
#scaffold-src-components-Login-demo-basic .icon-weibo {
    background-position: -24px -48px;
}
#scaffold-src-components-Login-demo-basic .icon-weibo:hover {
    background-position: 0 -48px;
}
#icon i{
    font-size: 25px;
    color: #CCCCCC;
    padding-right: 12px;
    margin-top: 5px;
}
#icon i:hover{
    cursor: pointer;
    color: #1890ff;
}
i#alipay{
    padding-left: 12px;
}
